@media screen and (max-width: 450px) {
	/* REVIEW: Home Section */
	.swipe-heading-container {
		position: absolute;
		z-index: 100;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		background-color: rgba(3, 1, 17, 0.205);
	}
	.home-center-heading {
		text-align: center;
		align-self: center;
		z-index: 100;
		font-weight: bolder;
		font-size: 18px !important;
		color: white;
	}
	.book-now-container {
		z-index: 100;
		position: absolute;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-content: center;
	}
	.book_now {
		margin-top: 100px !important;
		align-self: center;
		color: rgba(247, 244, 244, 0.877);
		background-color: blue;
		z-index: 100;
		padding: 8px 15px !important;
		border-radius: 10px;
		text-decoration: none;
		font-size: 12px !important;
		font-weight: 600;
		-webkit-border-radius: 10px;
		-moz-border-radius: 10px;
		-ms-border-radius: 10px;
		-o-border-radius: 10px;
	}
	.book_now:hover {
		text-decoration: none;
		background-color: rgba(247, 244, 244, 0.877) !important;
		padding: 8px 12px !important;
		color: blue !important;
		border: 2px solid blue;
		transition: 0.3s;
		-webkit-transition: 0.3s;
		-moz-transition: 0.3s;
		-ms-transition: 0.3s;
		-o-transition: 0.3s;
	}
	/* REVIEW: Gallery Section */
}